export default {
  name: "Donos",
  props: {},
  data() {
    return {
      exibirLista: true,
      lista: null,
      novo_dono: {
        dono_nome: null
      },
      update_dono: {
        dono_id: null,
        dono_nome: null
      },
      alert: false,
      confirm: false,
      prompt: false,
      promptUpdate: false
    };
  },
  methods: {
    confirmAction() {
      this.modalConfim = true;
    },
    listarDonos() {
      this.$donoService.getAll().then(response => {
        if (!response.error) {
          this.lista = response.map(obj => {
            return {
              nome: obj.dono_nome,
              id: obj.dono_id
            };
          });
        } else {
          throw new Error(response.error);
        }
      });
    },
    deleteDono(id, nome) {
      if (confirm(`Excluir ${nome}`)) {
        this.$donoService.deleteDono(id).then(response => {
          if (!response.error) {
            this.listarDonos();
            return;
          } else {
            throw new Error(response.error);
          }
        });
      }
    },
    addDono() {
      this.$donoService.addDono(this.novo_dono).then(response => {
        if (!response.error) {
          this.novo_dono.dono_nome = "";
          this.listarDonos();
          this.prompt = false;
          return;
        } else {
          throw new Error(response.error);
        }
      });
    },
    updateDono() {
      this.$donoService
        .updateDono(this.update_dono, this.update_dono.dono_id)
        .then(response => {
          if (!response.error) {
            this.novo_dono.dono_nome = "";
            this.listarDonos();
            return;
          } else {
            throw new Error(response.error);
          }
        });
    },
    findDono(id) {
      this.$donoService.findDono(id).then(response => {
        if (!response.error) {
          this.novo_dono.dono_nome = "";
          this.listarDonos();
          return;
        } else {
          throw new Error(response.error);
        }
      });
    }
  },
  mounted() {
    this.listarDonos();
  }
};
